body {
  font-family: 'Inconsolata', monospace;
  margin: 0;
}

.main-section {
  padding: 5rem 0;
  background: linear-gradient(to bottom, #caf0f8, #ffffff);
  height: 100vh;
}

.title-container {
  text-align: center;
  margin-bottom: 3rem;
}

.title {
  font-size: 2rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.title-underline {
  height: 0.2rem;
  width: 7rem;
  background: #60dbfb;
  margin: -1rem auto 0;
}


.section-center {
  width: 90vw;
  max-width: 1120px;;
  margin: 0 auto 2rem;
  display: grid;
  gap: 3rem 2rem;
  grid-template-columns: repeat(auto-fill, minmax(330px, 1fr));
}

.img-container {
  position: relative;
  height: 15rem;
}

img {
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

.item-link {
  text-decoration: none;
  border-radius: 0.25rem;
  background: #fff;
  box-shadow: 0 1px 3px 0 rgba(0,0,0,.1),0 1px 2px 0 rgba(0,0,0,.06);
  transition: 0.3s ease-in-out all;
}

.item-link:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 6px -1px rgba(0,0,0,.1),0 2px 4px -1px rgba(0,0,0,.06);
}

footer {
  text-align: center;
  padding: 1rem 1.25rem;
  background: #fff;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
}

footer h5 {
  font-size: 1.25rem;
  margin: 0;
  color: #0f172a;
  font-weight: 500;
  text-transform: capitalize;
  letter-spacing: 1px;
}